import videojs from 'video.js';
import vjsVolumeMenuButton from 'video.js/es5/control-bar/volume-menu-button';

function addHandler () {
	this.el_
		.addEventListener( 'mouseenter', () => {
			if ( !this.player.paused() ) {
				this.overlay.classList.remove( 'vjs-hidden' );
			}
		} );
	this.el_
		.addEventListener( 'mouseleave', () => {
			if ( !this.overlay.classList.contains( 'vjs-hidden' ) ) {
				this.overlay.classList.add( 'vjs-hidden' );
			}
		} );

	this.on( this.volumeBar, [ 'slideractive', 'focus' ], () => {
		this.player.addClass( 'vjs-volume-slider-active' );
	} );

	this.on( this.volumeBar, [ 'sliderinactive', 'blur' ], () => {
		this.player.removeClass( 'vjs-volume-slider-active' );
	} );
}

class AudioVolumeMenuButton extends vjsVolumeMenuButton {

	constructor ( player, options, ready ) {
		options.vertical = true;

		super( player, options, ready );

		const playerEl = player.el();

		this.player  = player;
		this.overlay = document.createElement( 'div' );

		this.overlay.classList.add( 'vjs-audio-volume-overlay' );
		this.overlay.classList.add( 'vjs-hidden' );

		playerEl.appendChild( this.overlay, playerEl.nextSibling );

		addHandler.call( this );
	}

}

videojs.registerComponent( 'AudioVolumeMenuButton', AudioVolumeMenuButton );

export default AudioVolumeMenuButton;



