import videojs from 'video.js';
import vjsComponent from 'video.js/es5/component';
import 'video.js/es5/control-bar/text-track-controls/subtitles-button';
import 'video.js/es5/tracks/text-track-display';

import './AudioPlayButton.component';
import './AudioVolumeMenuButton.component';
// import './AudioProgressBar.component';

class AudioButtonBar extends vjsComponent {

	constructor ( player, options, ready ) {
		options.children = [
			'AudioPlayButton',
			// 'AudioProgressBar',
			'TextTrackDisplay',
			'SubtitlesButton',
			'AudioVolumeMenuButton'
		];
		options.playerOptions = player.options_;
		super( player, options, ready );

		this.player_.childNameIndex_.textTrackDisplay = this.getChild( 'TextTrackDisplay' );
	}

	createEl () {
		return super.createEl( 'div', {
			className : 'vjs-audio-button-bar vjs-control-bar',
			dir :       'ltr'
		}, {
			role : 'group'
		} );
	}

}

videojs.registerComponent( 'AudioButtonBar', AudioButtonBar );

export default AudioButtonBar;



