import AbstractController from './../../../base/controller/Abstract.controller.js';

import videojs from 'video.js';
import AudioPlayerUI from './../components/AudioPlayerUI.component';

import vtt from 'videojs-vtt.js/lib/vtt';

import fasterdom from 'fasterdom';
import $ from 'jquery';

if ( !window.WebVTT ) {
	window.WebVTT = vtt.WebVTT;
}

const options = {
	audioPlayer : {
		controls :          true,
		autoplay :          false,
		fluid :             true,
		controlBar :        false,
		bigPlayButton :     false,
		posterImage :       false,
		// textTrackSettings : false,
		textTrackDisplay :  false,
		html5 :             {
			nativeTextTracks : false
		}
	}
};

/**
 * Class representing an audio player component
 * @extends { AbstractController }
 */
class AudioPlayerController extends AbstractController {

	constructor ( config ) {

		super( config );

		this.audioPlayer = null;
		this.$audio      = null;
		this.$sources    = null;

		this.wavesurferOptions                     = $.extend( true, {}, options.wavesurfer );
		this.audioPlayerOptions                    = $.extend( true, {}, options.audioPlayer );
		// this.audioPlayerOptions.plugins.wavesurfer = this.wavesurferOptions;
	}

	/**
	 * Initialises the controller.
	 * Has to be called after ready event.
	 *
	 * @param { jQuery } $root - jQuery wrapped DOM node
	 * @public
	 * @return { AudioPlayerController } instance
	 */
	init ( $root ) {
		this.$root = $root;

		this.$audio   = this.$root.find( 'audio' );
		this.$sources = this.$audio.find( 'source' );

		if ( this.$sources.length > 0 ) {

			fasterdom.measureInstant( () => {
				const tracks = this.$audio.attr( 'data-tracks' );

				if ( tracks ) {
					const parsedTracks = JSON.parse( tracks );

					this.audioPlayerOptions.tracks = parsedTracks;

					// $.each( parsedTracks, ( i, v ) => {
					// 	this.$audio.append( '<track kind="subtitles" label="' + v.label + '" src="' + v.src + '" srclang="' + v.lang + '" ' + ( i === 0 ? 'default' : '' ) + '></track>' );
					// } );
				}

				this.wavesurferOptions.src = this.$sources.eq( 0 ).attr( 'src' );
				this.audioPlayer           = videojs( this.$audio.get( 0 ), this.audioPlayerOptions );

				this._ui = AudioPlayerUI.setup( this.audioPlayer );

			} );
		} else {
			// no source files defined
		}

		return this;
	}
}

export default AudioPlayerController;



