import videojs from 'video.js';
import vjsComponent from 'video.js/es5/component';

import './WaveProgress.component';

class AudioSeekBar extends vjsComponent {

	constructor ( player, options, ready ) {
		options.children = [
			// 'loadProgressBar',
			// 'mouseTimeDisplay',
			// 'playProgressBar',
			// 'waveBar',
			// 'loadWaveBar',
			// 'playWaveBar',
			'waveProgress'
			// 'wavePlayProgress',
			// 'waveLoadProgress'
		];

		super( player, options, ready );
	}

	createEl () {
		return super.createEl( 'div', {
			className : 'vjs-audio-progress-holder'
		}, {
			'aria-label' : 'progress wave bar'
		} );
	}
	
}

videojs.registerComponent( 'AudioSeekBar', AudioSeekBar );

export default AudioSeekBar;



