import videojs from 'video.js';
import vjsComponent from 'video.js/es5/component';

import fasterdom from 'fasterdom';
import Device from 'device';

import PeaksGenerator from './../../helper/PeaksGenerator.helper';
import AudioWaveForm from './../waveform/AudioWaveForm.component';

function emitSize () {
	const compStyle = getComputedStyle( this.bars.top );

	[ this.waveForms.top, this.waveForms.bottom ].forEach( ( v ) => {
		if ( v === false ) {
			return;
		}
		v.resize(
			parseInt( compStyle.getPropertyValue( 'width' ), 10 )
		);
	} );
}

function timeUpdate () {
	const percent = Math.min( 100, ( this.player().currentTime() / this.player().duration() * 100 ).toFixed( 2 ) );

	[ this.waveForms.top, this.waveForms.bottom ].forEach( ( v ) => {
		if ( v === false ) {
			return;
		}
		v.updateProgress( percent );
	} );
}

function updateOnFrame () {
	if ( typeof this.player().controlBar.progressControl.seekBar !== 'undefined' ) {
		this.player().controlBar.progressControl.seekBar.update();
	}
	timeUpdate.call( this );

	if ( !this.player().paused() ) {
		fasterdom.onFrame( updateOnFrame.bind( this ) );
	}

	emitSize.call( this );
}

function addHandler () {
	this.player().ready( () => {
		emitSize.call( this );
	} );

	this.player().on( 'play', () => {
		fasterdom.onFrame( updateOnFrame.bind( this ) );
	} );

	Device.resizeEvent.subscribe( () => {
		emitSize.call( this );
	}, 250 );

	this.player().on( 'timeupdate', timeUpdate.bind( this ) );
}

class WaveProgress extends vjsComponent {

	constructor ( player, options, ready ) {
		super( player, options, ready );

		addHandler.call( this );
	}

	init () {
		if ( this.initialized === true ) {
			return;
		}

		this.peaks    = PeaksGenerator.generate( this.player().currentSrc() );
		// this.waveForm = new AudioWaveForm( this.player(), this.peaks );
		this.waveForms = {
			top :    new AudioWaveForm( this.player(), this.peaks ),
			bottom : new AudioWaveForm( this.player(), this.peaks )
		};

		this.initialized = true;
	}

	createEl () {
		this.init();

		this.node = super.createEl( 'div', {
			className : 'vjs-audio-wave-progress vjs-wave-progress'
		} );

		this.bars = {
			top : super.createEl( 'div', {
				className : 'wave-top-bars'
			} ),
			bottom : super.createEl( 'div', {
				className : 'wave-bottom-bars'
			} )
		};

		this.bars.top.appendChild( this.waveForms.top.getSVG() );
		this.bars.bottom.appendChild( this.waveForms.bottom.getSVG() );

		[ this.bars.top, this.bars.bottom ].forEach( ( v ) => {
			this.node.appendChild( v );
		} );

		return this.node;
	}

}

videojs.registerComponent( 'WaveProgress', WaveProgress );

export default WaveProgress;



