import videojs from 'video.js';
import vjsComponent from 'video.js/es5/component';

import './AudioProgressControl.component';

class AudioProgressBar extends vjsComponent {

	constructor ( player, options, ready ) {
		options.children = [
			'currentTimeDisplay',
			// 'timeDivider',
			'durationDisplay',
			'AudioProgressControl',
			'remainingTimeDisplay'
		];

		super( player, options, ready );

		// patch hardcoded video.js-internal controlBar dependency
		if ( !player.controlBar ) {
			player.controlBar = this;
		}
	}

	createEl () {
		return super.createEl( 'div', {
			className : 'vjs-audio-progress-bar vjs-control-bar',
			dir :       'ltr'
		}, {
			role : 'group'
		} );
	}

}

videojs.registerComponent( 'AudioProgressBar', AudioProgressBar );

export default AudioProgressBar;



