import svgjs from 'svg.js';

import AbstractController from './../../../../base/controller/Abstract.controller';
import { reducePeaks } from './../../helper/PeaksGenerator.helper';

const audioConfig = require( './../../../../../config/styles/audio.json' ).audio,
			fx          = {
				duration : 400,
				easing :   '>'
			};

function estimateWidth ( barCount ) {
	return Math.max( 0, barCount * ( audioConfig.bar.spacing + audioConfig.bar.width ) - audioConfig.bar.spacing );
}

function createBarRect ( height ) {
	return this.svg
					.rect( audioConfig.bar.width, height + 10 + '%' )
					.y( '-10%' )
					.radius( audioConfig.bar.width / 2 );
}

function adjustExistingBars ( peaks, count ) {
	for ( let i = 0; i < count; i++ ) {
		this.bars.get( i )
			.attr( 'height', peaks[ i ] + 10 + '%' );
	}
}

function animateClipRect ( width ) {
	return this.clipRect
		.stop()
		.animate( fx.duration, fx.easing )
		.attr( 'width', width );
}

function increaseBarCount ( from, to ) {
	const peaks           = reducePeaks( this.peaks, to ),
				clipWillBeWidth = estimateWidth( to );

	for ( let i = from; i < to; i++ ) {
		const rect = createBarRect.call( this, peaks[ i ] );

		if ( this.waving ) {
			barLoop.call( rect, 0 );
		}

		if ( i > 0 ) {
			rect.x( i * audioConfig.bar.width + i * audioConfig.bar.spacing + this.clipRect.x() );
		}

		[ this.bars, this.barsGroup ].map( ( v ) => v.add( rect ) );
	}

	adjustExistingBars.call( this, peaks, from );

	animateClipRect.call( this, clipWillBeWidth );
}

function decreaseBarCount ( from, to ) {
	const peaks           = reducePeaks( this.peaks, to ),
				clipWillBeWidth = estimateWidth( to );

	animateClipRect.call( this, clipWillBeWidth )
		.after( () => {
			for ( let i = from - 1; i > to - 1; i-- ) {
				const curElem = this.bars.get( i );

				this.bars.remove( curElem );
				curElem.remove();
			}
		} );

	adjustExistingBars.call( this, peaks, to );
}

function updateBarCount( count ) {
	const currentCount = this.bars.length();

	if ( currentCount !== count ) {
		( currentCount < count ? increaseBarCount : decreaseBarCount ).call( this, currentCount, count );
	}
}

function estimateDensity ( width ) {
	return Math.floor( ( width + audioConfig.bar.spacing ) / ( audioConfig.bar.width + audioConfig.bar.spacing ) );
}

function moveToCenter ( width, estimatedWidth ) {
	const toMove    = ( width - estimatedWidth ) / 2,
				isMoved   = this.clipRect.x(),
				deltaMove = toMove - isMoved;

	this.bars.dx( deltaMove );
	this.clipRect.dx( deltaMove );
}

function barLoop ( i ) {
	const heightValue  = this.attr( 'height' ),
				height       = parseFloat( heightValue, 10 ),
				heightNumber = new svgjs.Number( heightValue ),
				toValue      = [ Math.min( 100, height + 20 ), Math.max( 0, height - 20 )][ height < 50 ? 0 : 1 ] + '%',
				toNumber     = new svgjs.Number( toValue );
				
	this.data( 'origH', heightValue );

	this.stop().animate( 750, '<>', i * 50 ).attr( 'height', toValue ).loop( 0, true );
}

class AudioWaveBars extends AbstractController {

	constructor ( svg, peaks ) {
		super();

		this.svg       = svg;
		this.bars      = this.svg.set();
		this.barsGroup = this.svg.clip();
		this.clip      = this.barsGroup; // this.svg.clip();
		this.clipRect  = this.svg.rect( 0, '100%' ).fill( 'transparent' );

		this.peaks     = peaks;

		this.waving    = false;

		// this.clip.add( this.clipRect );
		// this.clip.clipWith( this.barsGroup );
	}

	setBarDensityForWidth ( width ) {
		const estimate = estimateDensity.call( this, width );

		this.currentWidht = width;

		updateBarCount.call( this, estimate );
		moveToCenter.call( this, width, estimateWidth( estimate ) );
	}

	wave () {
		if ( this.waving ) {
			return;
		}
		this.waving = true;

		this.bars.each( barLoop );
	}

	stopWave () {
		if ( !this.waving ) {
			return;
		}

		this.bars.each( function barLoop ( i ) { // eslint-disable-line prefer-arrow-callback
			const _hp = this.data( 'origH' );

			this.stop().animate( 250, '>' ).attr( 'height', _hp );
		} );

		this.waving = false;
	}

}

export default AudioWaveBars;



