import svgjs from 'svg.js';

import AbstractController from './../../../../base/controller/Abstract.controller';
import AudioWaveBars from './AudioWaveBars.component';

function setupElement () {
	// this.clip  = this.svg.clip();
	this.rects = {
		inactive : this.svg.rect( '100%', '100%' ).addClass( 'inactive' ),
		active :   this.svg.rect( '0%', '100%' ).addClass( 'active' )
	};

	[ this.rects.inactive, this.rects.active ].forEach( ( v ) => {
		v.clipWith( this.bars.clip );
		this.svg.add( v );
	} );
}

function addHandler () {
	this.player.on( 'play', () => {
		this.bars.wave();
	} );
	this.player.on( 'pause', () => {
		this.bars.stopWave();
	} );
}

class AudioWaveForm extends AbstractController {

	constructor ( player, peaks ) {
		super();
		
		this.player     = player;
		this.peaks      = peaks;

		this.svgElement = svgjs.create( 'svg' );
		this.svg        = svgjs( this.svgElement );

		// this.svg.attr( 'width', '100%' ).attr( 'height', '100%' );

		this.bars       = new AudioWaveBars( this.svg, this.peaks );

		this.currentWidth = 0;
		this.widthFactor  = 1;

		setupElement.call( this );
		addHandler.call( this );
	}

	getSVG ( barCount ) {
		return this.svgElement;
	}

	resize ( width ) {
		const container = this.player.controlBar.progressControl.el(),
					compStyle = getComputedStyle( container );

		this.currentWidth   = width;
		this.containerWidth = parseInt( compStyle.getPropertyValue( 'width' ), 10 );
		this.widthFactor    = this.currentWidth / this.containerWidth;

		this.bars.setBarDensityForWidth( width );
	}

	updateProgress ( percent ) {
		if ( !percent ) {
			return;
		}

		const d         = ( 1 - this.widthFactor ) / 2 * 100;
		let realPercent = ( percent - d ) / this.widthFactor;

		realPercent = Math.max( 0, Math.min( 100, realPercent ) );
		
		this.rects.active.width( realPercent + '%' );
	}

}

export default AudioWaveForm;



