import crypto from 'crypto';

import AbstractController from './../../../base/controller/Abstract.controller';

const possibleChars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789',
			redPeaksCache = {};

function interpolatePeaks ( before, after, atPoint ) {
	return before + ( after - before ) * atPoint;
}

function reducePeaks ( peaks, to ) {
	const peaksKey = JSON.stringify( peaks );

	if ( redPeaksCache[ peaksKey ] && redPeaksCache[ peaksKey ][ to ] ) {
		return redPeaksCache[ peaksKey ][ to ];
	}

	const factor = ( peaks.length - 1 ) / ( to - 1 ),
				newPeaks = new Array( to );

	newPeaks[ 0 ] = peaks[ 0 ];
	for ( let i = 1; i < to - 1; i++ ) {
		const tmp = i * factor,
					before = Math.floor( tmp ),
					after = Math.ceil( tmp ),
					atPoint = tmp - before;

		newPeaks[ i ] = interpolatePeaks( peaks[ before ], peaks[ after ], atPoint );
	}
	newPeaks[ to - 1 ] = peaks[ peaks.length - 1 ];

	if ( !redPeaksCache[ peaksKey ] ) {
		redPeaksCache[ peaksKey ] = {};
	}
	redPeaksCache[ peaksKey ][ to ] = newPeaks;
	return redPeaksCache[ peaksKey ][ to ];
}


function getRandomArbitrary ( min, max ) {
	return Math.random() * ( max - min ) + min;
}

function strHexToDec ( str ) {
	return parseInt( str, 16 );
}

function correctValue ( v ) {
	let val = v === 0 ? getRandomArbitrary( 6, 15 ) : v;

	val = val / 15 * 100;
	val = val < 50 ? ( 50 - val ) * 2 * 0.25 + val : val;

	return val;
}

function charToPeak ( char ) {
	const peak = correctValue( strHexToDec( char ) );

	return peak;
}

