function createPeakMapFromString ( str, max = 88 ) {
	const hash = crypto.createHash( 'sha512' );

	hash.update( str );

	const strHash   = hash.digest( 'hex' ), // eslint-disable-line one-var
				mapLength = Math.max( strHash.length, max ),
				map       = new Array( mapLength );

	for ( let i = 0; i < mapLength; i++ ) {
		map[ i ] = charToPeak( strHash[ i ] );
	}

	return map;
}

class PeaksGenerator extends AbstractController {

	generate ( str = null, frames = -1 ) {
		if ( str !== null ) {
			return this.generateFromString( str, frames );
		} else {
			return this.random( frames );
		}
	}

	random ( frames = -1 ) {
		let text = '';

		for ( let i = 0; i < frames; i++ ) {
			text += possibleChars.charAt( Math.floor( Math.random() * possibleChars.length ) );
		}

		return this.generateFromString( text, frames );
	}

	generateFromString ( str, frames = -1 ) {
		if ( typeof this.cache[ str ] === 'undefined' ) {
			this.cache[ str ] = createPeakMapFromString( str, frames );
		}
		
		return this.cache[ str ];
	}

}

const instance = new PeaksGenerator(); // eslint-disable-line one-var

export default instance;
export { reducePeaks };




