import BaseModule from './../Base.module';
import AudioPlayerController from './controller/AudioPlayer.controller';

class AudioModule extends BaseModule {

	constructor ( config ) {

		super( config );
	}

	init () {
		super.init();

		const controllerInstance = new AudioPlayerController();

		this.slider = controllerInstance.init( this.node );
	}
}

export default AudioModule;


