import videojs from 'video.js';
import vjsProgressControl from 'video.js/es5/control-bar/progress-control/progress-control';

import './AudioSeekBar.component.js';

class AudioProgressControl extends vjsProgressControl {

	constructor ( player, options, ready ) {
		options.children = [
			'seekBar',
			'audioSeekBar'
		];

		super( player, options, ready );

		// patch hardcoded video.js-internal controlBar dependency
		if ( !player.controlBar ) {
			player.controlBar = {};
		}
		player.controlBar.progressControl = this;
	}

	createEl () {
		return super.createEl( 'div', {
			className : 'vjs-audio-progress-control vjs-progress-control vjs-control'
		} );
	}

}

videojs.registerComponent( 'AudioProgressControl', AudioProgressControl );

export default AudioProgressControl;



