import videojs from 'video.js';
import vjsPlayToggle from 'video.js/es5/control-bar/play-toggle';

class AudioPlayButton extends vjsPlayToggle {

	buildCSSClass () {
		return 'vjs-audio-play-button vjs-audio-button ' + super.buildCSSClass();
	}
	
}

videojs.registerComponent( 'AudioPlayButton', AudioPlayButton );

export default AudioPlayButton;



