import AbstractController from './../../../base/controller/Abstract.controller';

import './ui/AudioButtonBar.component';
import './ui/AudioProgressBar.component';

const audioConfig = require( './../../../../config/styles/audio.json' );

class AudioPlayerUI extends AbstractController {

	constructor ( player ) {
		super();

		this.player = player;
		this.config = audioConfig;
		this.init();
	}

	static setup ( player ) {
		return new AudioPlayerUI( player );
	}

	static getConfig () {
		return this.config;
	}

	init () {
		this.player.addChild( 'AudioProgressBar' );
		this.player.addChild( 'AudioButtonBar' );
	}

}

export default AudioPlayerUI;



